@use './abstracts/variables/colors' as *;

#adminmenu {

    a[href="admin.php?page=apop-settings"] {

        .wp-menu-image {

            content: url(../images/admin/menu.svg);
            padding: 7px 0;
            padding-left: 8px;
            transition: all .1s ease-in-out;
            width: 20px;
            height: 20px;

            img {
                display: none;
            }
        }

        &:hover {

            .wp-menu-image {
                content: url(../images/admin/menu-hover.svg);
            }
        }

    }

    a[href="admin.php?page=apop-settings"].wp-menu-open {

        .wp-menu-image {
            content: url(../images/admin/menu-active.svg);
        }

        &:hover {

            .wp-menu-image {
                content: url(../images/admin/menu-active.svg);
            }
        }
    }

    a[href="admin.php?page=accessibility-onetap-pro"] {
        background: $primary-color;
        box-shadow: none;
        color: $light-color;
        font-weight: 600;
        border-radius: 8px;
        margin: 3px 10px 0;
        text-align: center;
    }

    a[href="admin.php?page=accessibility-onetap-settings"] {

        .wp-menu-image {

            content: url(../images/admin/menu.svg);
            padding: 7px 0;
            padding-left: 8px;
            transition: all .1s ease-in-out;
            width: 20px;
            height: 20px;

            img {
                display: none;
            }
        }

        &:hover {

            .wp-menu-image {
                content: url(../images/admin/menu-hover.svg);
            }
        }

    }

    a[href="admin.php?page=accessibility-onetap-settings"].wp-menu-open {

        .wp-menu-image {
            content: url(../images/admin/menu-active.svg);
        }

        &:hover {

            .wp-menu-image {
                content: url(../images/admin/menu-active.svg);
            }
        }
    }		

    .wp-submenu {

        a[href="admin.php?page=accessibility-onetap-pro"]:hover,
        a[href="admin.php?page=accessibility-onetap-pro"]:focus {
            background: $primary-color;
            color: $light-color;
        }
    }    

}